import React, { useEffect, useState } from 'react'
import { View } from 'react-native'
import { Avatar, Card, ListItem, Text } from 'react-native-elements'
import { CHAIN_ETHEREUM, getNodeURL } from 'staker-freenodes'

// https://docs.ethers.io/v5/cookbook/react-native/#cookbook-reactnative-security
import 'react-native-get-random-values'
import '@ethersproject/shims'
import { ethers } from 'ethers'
import BN from 'bignumber.js'

import { isAddr } from 'helpers/address'

import DefiExplorer from './DefiExplorer'
import ABI from './abi.json'

// const v3Contract = '0xaDfc6460233221eCa99daC25d00f98d32eA3989e'
const v2Contract = '0x06FE76B2f432fdfEcAEf1a7d4f6C3d41B5861672'

function getNormalizedNumber(number, decimals) {
  return new BN(number).dividedBy(new BN(10).pow(decimals))
}

export const DefiStakes = ({ chainId, term }) => {
  const [balanceOnProtocls, setBalance] = useState([])
  const [loading, setLoading] = useState(true)
  const isValidQuery = isAddr(term) && chainId === CHAIN_ETHEREUM
  if (!isValidQuery) return null

  useEffect(() => {
    async function fetching() {
      const provider = new ethers.providers.JsonRpcProvider(
        getNodeURL(CHAIN_ETHEREUM)
      )
      const contract = new ethers.Contract(v2Contract, ABI, provider)
      // const names = await contract.functions.getTokenAdapterNames()
      const balanceOnProtocls = await contract.getProtocolBalances(term, [
        'Aave',
        'Aave V2',
        'Aave V2 • Variable Debt',
        'Aave V2 • Stable Debt',
        'Aave • Staking',
        'Aave • Uniswap Market',
        'Uniswap V2',
        'SushiSwap',
        'SushiSwap • Staking',
        '1inch Liquidity Protocol',
        '1inch LP • Staking',
      ])
      setBalance(balanceOnProtocls)
      setLoading(false)
    }

    fetching()
  }, [term])

  if (loading) {
    return <Text>query...</Text>
  }

  // return <Text>{balance}</Text>
  return (
    <View style={{ paddingTop: 7, flexDirection: 'column' }}>
      <Card.Title>DEFI</Card.Title>
      <DefiExplorer chainId={chainId} term={term} />
      <Card.Divider />
      {balanceOnProtocls.map((protocol) => {
        // console.log('%O', protocol)
        return (
          <>
            <Card.Title key={protocol.metadata.name}>
              {/* <Avatar
                  size='small'
                  source={{
                    uri: protocol.metadata.logo,
                  }}
                /> */}
              {protocol.metadata.name}
            </Card.Title>
            {protocol.adapterBalances.length > 0 &&
              protocol.adapterBalances.map((protocolBalances) => {
                // Each adapter could either be an Asset or Debt on the protocol
                return (
                  <>
                    {protocolBalances.balances.map((balance) => {
                      // console.log(jsonStringify(balance))
                      const position = {
                        Token: balance.base.metadata.symbol,
                        Balance: getNormalizedNumber(
                          balance.base.amount.toString(),
                          balance.base.metadata.decimals
                        ).toString(),
                      }

                      // If aset is a derivative then there will be underlying assets
                      if (balance.underlying.length > 0) {
                        const underlying = []
                        balance.underlying.forEach((asset) => {
                          underlying.push({
                            Component: asset.metadata.symbol,
                            Amount: getNormalizedNumber(
                              asset.amount.toString(),
                              asset.metadata.decimals
                            ).toString(),
                          })
                        })
                        position.Underlying = underlying
                      }
                      // console.log('Position:', position)
                      return (
                        <ListItem key={position.Token}>
                          <ListItem.Content>
                            <ListItem.Title>
                              {position.Token} {position.Balance}
                            </ListItem.Title>
                            <ListItem.Subtitle>
                              {position.Underlying.map(
                                (token) => `${token.Component} ${token.Amount}`
                              ).join(' ')}
                            </ListItem.Subtitle>
                          </ListItem.Content>
                        </ListItem>
                      )
                    })}
                  </>
                )
              })}
            <Card.Divider />
          </>
        )
      })}
    </View>
  )
}

export default DefiStakes
